import React, { useEffect, useState } from "react";

import { getStore, updStore, gen16CodeId,  parseDateMMDDYYYY, parseQuery, qryStore, fetchEncryptStream, compareJSON } from "../../../stores/Utils";

import "./styles.css"

import { OpenModal } from "../../../components/DialogHRM";
import {  getall_operations, delete_Comprobante, validateComprobantes	 } from "../services/server_fetching";

import {ViewComprobante_Cmpt } from "..";
import { useLocation, useNavigate } from "react-router";
import { SvgIcon_Cmpt } from "../../../components";
import DeleteAlert from "./deleteAlert";

const ViewComprobante = ViewComprobante_Cmpt();

const Svg = SvgIcon_Cmpt();



const ComprobanteItem  = ({ data, handleRefreshAll, updRvw }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	let location = useLocation();
	const navigate = useNavigate();

	



	useEffect(() => {
			if(!initialize){
				setInitialize(true);
				setTimeout(() => {
					const searchHash = location.search?.split("?")?.[1];
					const router = parseQuery(searchHash);
					if(router?.cId && router?.cId=== data.comprobanteId){
						openViewComprobante();
						window.scrollTo(0, 0);
					}
					
				}, 350);
			}
	},[location.search]);

	
	const updObs =  () => {
		set_obs(gen16CodeId())
	}

	

	const refresh =  () => {
		getall_operations(updObs)
	}

	const closeEv =  () => {
		handleRefreshAll();
		navigate({
			search: ``,
		});
		
	}



	
	const updIsUnderReview = async () => {
		if(getStore("comp_under_review")?.balanceId){
			let frm2Sv = {...getStore("comp_under_review")};
			let inReview = {...frm2Sv.inReview};
			
			let bdyq2 = {...getStore(qryStore())?.upd_balances};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId,
				balanceId: getStore("comp_under_review")?.balanceId,
			};
			
			if(inReview[data?.comprobanteId]){
				delete inReview[data?.comprobanteId];
			}else{
				inReview[data?.comprobanteId] = true;
			}
			
			bdyq2.data2update={inReview};
			const res = fetchEncryptStream(bdyq2);
			const td = await res;
			
			// console.log(bdyq2)
			if (td) {
				frm2Sv.inReview = inReview;
				updStore("comp_under_review", frm2Sv);
				set_obs(gen16CodeId())
			}
		}
		else{
			let frm2Sv = {...getStore("comp_under_review")};
			frm2Sv.balanceId = gen16CodeId();
			frm2Sv.type = "comp_under_review";
			frm2Sv.inReview = {};
			frm2Sv.inReview[data?.comprobanteId] = true;
			let bdyq2 = {...getStore(qryStore())?.add_balances};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			};
			bdyq2.form=frm2Sv;
			const res = fetchEncryptStream(bdyq2);
			const td = await res;
			if (td) {
				updStore("comp_under_review", frm2Sv);
				set_obs(gen16CodeId())
			}
		}
		validateComprobantes(getStore("comprobante_by_doc"));
		updRvw(gen16CodeId())
	}


	
	const openViewComprobante =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			const searchHash = location.search?.split("?")?.[1];
			const router = parseQuery(searchHash);
			if(!(router?.cId && router?.cId=== data.comprobanteId)){
				navigate({
					search: `?cId=${data.comprobanteId}`,
				});
			}
			updStore("show_comprobante", data);
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "80vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: closeEv  };
			modaldata["content"] = <ViewComprobante confirm={refresh} item={data} />
			OpenModal(modaldata);
		}
	}

	const deleteComprobante =  () => {
		delete_Comprobante(data?.operations, handleRefreshAll)
	}
	

	
	
	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <DeleteAlert confirm={deleteComprobante} item={data} />
			OpenModal(modaldata);
		}
	}


	let isInReview = getStore("comp_under_review")?.inReview?.[data?.comprobanteId];
	
	let hasError = getStore("comp_under_review")?.hasError?.[data?.comprobanteId];
	
	


	return (
		<div className="item_acc">
			<div className="_dsplFlx operation_item_box" >
				<div className="icon_revision" onClick={updIsUnderReview}>
					<Svg 
						name={hasError ? "alert":!isInReview?"check_circle":"alert"} 
						color={hasError ? "var(--hrm_palette-selected-nav-text)":!isInReview?"var(--hrm_palette-success-fg)":"var(--hrm_palette-color-orange-800)"}
					/>
				</div>
				<div className="_dsplFlx tr_bar_90" onClick={openViewComprobante}>
					<div className=" tr_bar_15" >
						{parseDateMMDDYYYY(data?.date)} 
					</div>
					<div className=" tr_bar_25" style={hasError ?{color:"var(--hrm_palette-selected-nav-text)"}:{}}>
						{data?.comprobanteId }
					</div>
					<div className=" tr_bar_55" style={{marginLeft: 9}}>
					{hasError ?
						<div className="msg_alert_title">
							El comprobante contiene errores
						</div>
					:<span>
					{data?.description }
					</span>}
						
					</div>
				</div>
				<div className="flexSpace"/>
				<div className="icon tr_bar_5" onClick={OpenDeleteAlert}>
					<Svg name={"outline_delete"} color={"#c62828"}/>
				</div>
				
			</div>
			
		</div>
	)
}
export default ComprobanteItem;



